import React, { useEffect, useState } from 'react';
import ReactPlayer from 'react-player';
import {Link} from 'react-router-dom';
import './input.css';
import $ from 'jquery';
import { MDBInput } from 'mdbreact';

function Input() {
    const[name, setName] = useState("");
    const[list, setList] = useState([]);

    const handleSubmit=(e) => {
        e.preventDefault();
        console.log(name)
        const date = {name}
        if(name){
            setList((ls) => [...ls, date])
            setName("")
            
        }
    }
    return (
        <>
        <div className="content">
            <div className="box">
                <div className="input_title">
                    <span className="h1">The future of driving will... </span> 
                </div>

                <div className="input_field">
                    <form onSubmit={handleSubmit}>
                        <div className="input">
                            <MDBInput name="name" label=" " id="input_style" value={name} onChange={(e) => setName(e.target.value)}/>
                            {/* <input name="name" id="input_style" placeholder="What do I think of this?" value={name} onChange={(e) => setName(e.target.value)} /> */}
                            {/* <button>Submit</button> */}
                        </div>
                        <br />
                    </form>
                    {
                        list.map((a) => <div>
                            <ul className="result">{a.name}</ul>
                            </div>)
                    }
                </div>

                {/* <div className="result_list">
                {
                        list.map((a) => <div>
                            <ul className="result">{a.name}</ul>
                            </div>)
                    }
                </div> */}
            </div>
        </div>
        </>
    );
}

export default Input;